.matador-job-meta {
  display: block;
  margin: 0 0 1rem;
  padding: 0;
  overflow: auto;

  .matador-job-meta-field {
    display: inline-block;
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
}

.matador-job-meta-default {
  padding: .5rem 1rem;
  font-size: 1rem;
  line-height: 1rem;
  background: #eee;

  .matador-job-meta-field {
    padding: .25rem 0;
    margin: .5rem .75rem .5rem 0;
    &:last-child {
      margin: .5rem 0 .5rem;
    }
  }

  .matador-job-field-employmentType {
    background: #ccc;
    padding: .25rem .5rem;
    .matador-job-meta-label {
      @extend .matador-screen-reader-text;
    }
  }

  .matador-job-field-job_general_location {
    padding-left: 2rem;
    position: relative;
    &:before {
      content: url('../images/dashicons-location-alt.svg');
      display: block;
      width: 1.75rem;
      height: 1.75rem;
      margin-right: .25rem;
      position: absolute;
      left: 0;
      top: -.25rem;
    }
    .matador-job-meta-label {
      @extend .matador-screen-reader-text;
    }
  }
  .matador-job-field-bullhorn_job_id {
    float: right;
  }
}

.matador-job-header .matador-job-meta {
  margin-bottom: 0;
}