.matador-job-alerts-subscribe {

  input[type='text'] {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  @media only screen and ( min-width: 600px ) {
    .matador-subscribe-to-alerts-firstname,
    .matador-subscribe-to-alerts-lastname {
      width: 48%;
      margin-right: 10px;
      float: left;
    }
    .matador-subscribe-to-alerts-lastname {
      margin-left: 10px;
      margin-right: 0;
    }
  }
  .matador-subscribe-to-alerts-lastname + p {
    clear: both;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .matador-subscribe-to-alerts-groups {
    display: flex;
    flex-flow: row wrap;
  }
  .matador-label-categories-wrap {
    flex: 0 1 160px;
    padding-right: 10px;

    .matador-label-categories {
      font-weight: normal;
      font-style: italic;
    }
    label {
      font-weight: normal;
    }
  }
  .matador-job-alerts-confirm-opt-in {
    font-weight: normal;
    margin-top: 20px;
    margin-bottom: 20px;
    padding-left: 25px;
    position: relative;
    input {
      position: absolute;
      top: 4px;
      left: 0;
    }

  }
  .matador-job-alerts-error {
    border: 1px $error-color solid;
    color: rgba( $error-color, 0.18);
    padding: 10px;
  }
}