.matador-pagination {
  text-align: center;
  margin-bottom: 2rem;

  .page-numbers {
    font-size: 1em;
    font-weight: 600;
    line-height: 0.2em;
  }

  .prev.page-numbers,
  .next.page-numbers {
    display: inline-block;
    background: $primary-background none repeat scroll 0 0;
    border-radius: 4px;
    color: $primary-foreground;
    padding: 6px 12px;
    text-align: center;
    width: auto;
    box-shadow: none;
    -webkit-box-shadow: none;
    font-size: 1em;
    font-weight: 600;
  }

  .prev.page-numbers {
    float: left;
  }
  .next.page-numbers {
    float: right;
  }

}