

.matador-application {
  box-sizing: border-box;
  margin-top: 2rem;
  margin-bottom: 4rem;
  position: relative;

  .single-matador-job-listings & {
    border-top: 1px solid #eee;
    margin-top: 2rem;
    padding-top: 2rem;
    border-bottom: 1px solid #eee;
    margin-bottom: 2rem;
    padding-bottom: 2rem;
  }

  p,
  label {
    margin: 0;
    padding: 0;
  }

  ul, ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  ul li,
  ol li {
    margin: 0;
    padding: 0 0 .5rem 0;
  }
  select{
    width:100%;
    height: 6em;
    font-size: 1.25rem;
  }
  input[type='text'],
  input[type='email'],
  input[type='password'],
  input[type='tel'],
  input[type='date'],
  textarea,
  select:not([multiple]){
    padding: .5rem;
    margin: 0;
    width: 100%;
    height:2.4em;
    font-size: 1.25rem;
  }

  input[type='radio'],
  input[type='checkbox'] {
    width: auto;
  }

  input[type='radio'] {
    -webkit-appearance: radio;
  }

  input[type='checkbox'] {
    -webkit-appearance: checkbox;
  }

  textarea {
    min-height: 180px;
  }





  input.error:not([type='submit'],[type='reset']){
    border: 1px solid $error-color;
  }

  .matador-field-group {
    padding-top: .5rem;
    padding-bottom: 1rem;
  }

  .matador-field-group .matador-field-label {
    margin: 0 0 .5rem;
  }

  .matador-field-group .matador-label label {
    line-height: 1.25rem;
  }

  .matador-required .matador-label label:after {
    content: ('*');
    color: $error-color;
    position: relative;
    top: -.25rem;
    font-size: 1.25rem;
    display: inline-block;
  }

  .error {
    font-weight: normal;
    margin-top: .5rem;
    font-style: italic;
    color: $error-color;
  }

  // This is the honeypot field
  .matador-field-job-seeker {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    margin: 0;
    padding: 0;
    border: 0;
    word-wrap: normal !important;
  }

  .matador-field-type-checkbox,
  .matador-field-type-radio {

    label {
      padding-left: 1.5rem;
      position: relative;
      font-weight: normal;
    }
    input {
      position: absolute;
      left: 0;
      top: .25rem;
    }

  }

  .matador-field-type-file {
    .for-file {
      max-width: 100%;
      font-size: 16px;
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      display: inline-block;
      overflow: hidden;
      padding: 10px 20px;
      color: #666;
      border: 1px solid #bbb;
      border-radius: 3px;
      width: 100%;
      span {
        display: inline-block;
        margin-left: 6px;
      }
      svg {
        width: 1em;
        height: 1em;
        vertical-align: middle;
        fill: currentColor;
        margin-top: 4px;
        margin-right: 4px;
      }
      .no-js & {
        display: none;

      }
    }
    .inputfile {
      width: 0.1px;
      height: 0.1px;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      z-index: -1;
    .no-js & {
      width: auto;
      height: auto;
      opacity: 1;
      overflow: auto;
      position: static;
      z-index: 1;
    } }
  }

  .matador-field-privacy_policy_opt_in {
    margin: .5rem 0 1rem;
    padding: 1rem 0;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
  }

  .matador-submit {
    margin-top: 1rem;
  }

  #matador-upload-overlay {

    display: none;
    width: 104%;
    height: 104%;
    position: absolute;
    top: -2%;
    left: -2%;

    .matador-upload-overlay-background {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: whitesmoke;
      opacity: .6;
      filter: alpha(opacity=70);
      border-radius: 8px;
      -moz-border-radius: 8px;
      -webkit-border-radius: 8px;
      border: 0 solid $error-color;
    }

    .matador-upload-overlay-message {
      margin-top: -50%;
      text-align: center;
      border: 1px solid;
      border-radius: 8px;
      -moz-border-radius: 8px;
      -webkit-border-radius: 8px;
      margin-left: 2%;
      margin-right: 2%;
      padding: 40px;
      background: #fff;
      opacity: 1;
      filter: alpha(opacity=100);
      position: absolute;
    }

    .spinner {
      background: url( '../images/spinner.gif' ) no-repeat;
      -webkit-background-size: 20px 20px;
      background-size: 20px 20px;
      display: inline-block;
      vertical-align: middle;
      opacity: .7;
      filter: alpha(opacity=70);
      width: 20px;
      height: 20px;
      margin: -4px 6px 0;
    }
  }
}