.matador-screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.matador-deprecated-notice {
  border: 4px solid orange;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #ffc;
  p:last-child {
    margin-bottom: 0;
  }
}